<template>
    <div>
        <div>
            <div  class="left">
                <img :src="result.images.small" alt="">
            </div>
            <div class="right">
                <div>{{result.original_title}}</div>
            </div>
            <div class="conntent">
                <p>{{result.summary}}</p>
            </div>
        </div>
    </div>
</template>
<style scoped>

</style>
<script>
export default {
    data(){
        return{
            result:{}
        }
    },
    created(){
        let proxy='https://bird.ioliu.cn/v2?url=';
        let url=`https://api.douban.com/v2/movie/subject/${this.$route.params.id}`
        axios.get(proxy+url).then((res)=>{
            console.log(res);
            this.result=res.data;
            console.log(res.data)
        }).catch((res)=>{
            console.log("失败");
        })
    },
}
</script>